import { Search, searchConfType } from "../../components/Search";
import { useState, useEffect } from "react";
import { Table, Dialog, Button, Radio, MessagePlugin } from "tdesign-react";
import { ErrorCircleFilledIcon } from "tdesign-icons-react";
import { apiFenceList, apiDeleteFence } from "../../api/api";
import  NoData  from '../../components/NoData'

type trajectoryData = {
  name: string;
  id: string;
  serverName: string;
  serverId: string;
  trajectoryLength: string;
  time: string;
  createTime: string;
}[];

function Fence(props: any) {
  const [visible, setVisible] = useState(false);
  const searchConf: searchConfType[] = [
    { label: "围栏名称:", key: "fenceName", value: "", callback: searchHandle },
    { label: "围栏ID:", key: "fenceId", value: "", callback: searchHandle },
  ];

  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(10);
  const [Data, setData] = useState([]);
  const [total, setTotal] = useState(30);
  const [deleteDish, setDeleteDish] = useState("");
  const [provider, setProvider] = useState("BAIDU");
  const [searchValue, setSearchValue] = useState({});

  // 生命周期调用数据
  useEffect(() => {
    getData();
  }, []);

  // 获取数据
  function getData(items?: any) {
    apiFenceList({
      page: items?.page ?? page,
      pageSize: items?.pageSize ?? pageSize,
      fenceName: items?.fenceName,
      fenceId: items?.fenceId,
      provider:items?.provider ?? provider,
    }).then((res: any) => {
      const { data } = res;
      console.log(data);
      if (res.code === 0) {
        setData(data.items);
        setTotal(data.total);
      } else {
        console.log("请求出错！");
      }
    });
  }

  // 搜索功能
  function searchHandle(searchVal: any) {
    setPage(1);
    setPageSize(10);
    setSearchValue(searchVal);
    getData({ page: 1, pageSize: 10, ...searchVal });
  }
  // 关闭弹框
  const handleFooterClose = () => {
    setVisible(false);
  };

  // 删除功能
  const deleteHandle = () => {
    if (deleteDish) {
      apiDeleteFence(deleteDish);
      setDeleteDish("");
      setVisible(false);
    } else {
      MessagePlugin.error("删除请求出错了");
    }
  };
  // 更改单选类型 - 不同服务商切换
  const changeHandle = (val: any) => {
    setProvider(val);
    getData({searchValue, provider:val});
  };
  return (
    <div className="container fence">
      <div className="searchCont">
        <Search searchConf={searchConf}></Search>
        <div className="tdesign-demo-block-row">
          <Radio.Group value={provider} onChange={changeHandle}>
            <Radio value="BAIDU">百度地图</Radio>
            <Radio value="AMAP">高德地图</Radio>
            {/* <Radio value="TMAP">腾讯地图</Radio> */}
          </Radio.Group>
        </div>
      </div>
      <div className="tableCont">
        {Data && Data.length > 0? (<Table
          data={Data}
          columns={[
            {
              align: "left",
              className: "test",
              ellipsis: true,
              colKey: "name",
              title: "围栏名称",
              minWidth: 200,
            },
            {
              align: "left",
              className: "test4",
              ellipsis: true,
              colKey: "fenceId",
              title: "围栏ID",
              minWidth: 200,
            },
            {
              align: "left",
              className: "test4",
              ellipsis: true,
              cell({ row }) {
                switch (row?.provider) {
                  case "BAIDU":
                    return <>百度地图</>;
                  case "AMAP":
                    return <>高德地图</>;
                  case "QQ":
                    return <>腾讯地图</>;
                  default:
                    return null;
                }
              },
              title: "服务商名称",
              minWidth: 200,
            },
            {
              align: "left",
              className: "test4",
              ellipsis: true,
              colKey: "serverId",
              title: "服务ID",
              minWidth: 200,
            },
            {
              align: "left",
              className: "test4",
              ellipsis: true,
              colKey: "created",
              title: "创建时间",
              minWidth: 200,
            },
            {
              align: "left",
              className: "test4",
              ellipsis: true,
              colKey: "updated",
              title: "更新时间",
              minWidth: 200,
            },
            {
              colKey: "op",
              width: 140,
              title: "操作",
              fixed: "right",
              cell(record) {
                return (
                  <>
                    <a
                      className="link"
                      onClick={() => {
                        props.history.push({
                          pathname: `/fence/${record.row.id}`,
                          state: record.row
                        });
                      }}
                    >
                      查看
                    </a>
                    <a className="link" onClick={() => {
                      setDeleteDish(record.row.fenceId);
                      setVisible(true);
                    }}>
                      删除
                    </a>
                  </>
                );
              },
            },
          ]}
          rowKey="index"
          verticalAlign="top"
          bordered
          hover
          pagination={{
            defaultCurrent: page,
            defaultPageSize: pageSize,
            total,
            showJumper: true,
            onChange(pageInfo) {
              setPageSize(pageInfo.pageSize);
              setPage(pageInfo.current);
              getData();
            },
          }}
        ></Table>) : (<NoData />)}
      </div>
      <Dialog
        header={
          <>
            <ErrorCircleFilledIcon style={{ color: "#ED7B2F" }} />
            <span>确定要删除这条轨迹吗？</span>
          </>
        }
        footer={
          <>
            <Button
              theme="default"
              variant="outline"
              onClick={() => setVisible(false)}
            >
              取消
            </Button>
            <Button theme="danger" onClick={() => setVisible(false)}>
              删除
            </Button>
          </>
        }
        visible={visible}
        onClose={handleFooterClose}
      ></Dialog>
    </div>
  );
}

export default Fence;
